import * as React from "react"
import {Grid} from "@mui/material";
import Typography from "@mui/material/Typography";

import {FC, useEffect, useState} from "react";
import {Link} from "gatsby";

// @ts-ignore
import GithubIcon from "../images/github.png";
import {ViewManager} from "./ViewManager";

const linkStyle = {
    color: `black`,
    textDecoration: `none`,
    padding: `0px 7px`,
}

type ProjectProps = {
    name: string,
    date: string,
    body: string,
    image?: string,
    url: string
}

const MobileProject: FC<ProjectProps> = ({
    name,
    date,
    body,
    image,
    url
}) => {

    return (
        <Grid container spacing={2} style={{height: "40%"}}>
            <Grid item>
                <Link to={url} style={linkStyle}>
                    <Grid container spacing={1}>
                        {
                            url &&
                            <Grid item>
                                <img src={GithubIcon} style={{width: "25px", height: "25px"}} alt={"github"} />
                            </Grid>
                        }
                        <Grid item>
                            <Typography variant="h5">
                                {name}
                            </Typography>
                        </Grid>
                    </Grid>
                    <Typography variant="body2" color={"gray"}>
                        {date}
                    </Typography>
                </Link>
            </Grid>
            <Grid item justifyContent={"center"}>
                {
                    image &&
                    <img src={image} alt={"project_image"} style={{maxWidth:"100%"}}/>
                }
            </Grid>
            <Grid item>
                <Typography variant="body1">
                    {body}
                </Typography>

            </Grid>
        </Grid>
    )
}

const BrowserProject: FC<ProjectProps> = ({
    name,
    date,
    body,
    image,
    url
}) => {

    return (
        <Grid container spacing={2} style={{height: "40%"}} justifyContent={"center"}>
            <Grid item xs={12}>
                <Link to={url} style={linkStyle}>
                    <Grid container spacing={1}>
                        {url && <Grid item> <img src={GithubIcon} style={{width: "30px", height: "30px"}} alt={"github"} /></Grid>}
                        <Grid item>
                            <Typography variant="h5">
                                {name}
                            </Typography>
                        </Grid>
                    </Grid>
                    <Typography variant="body2" color={"gray"}>
                        {date}
                    </Typography>
                </Link>
            </Grid>
            <Grid item xs={6}>
                <Typography variant="body1">
                    {body}
                </Typography>
            </Grid>
            <Grid item xs={6} justifyContent={"center"}>
                {
                    image &&
                    <img src={image} alt={"project_image"} style={{maxWidth:"100%"}}/>
                }
            </Grid>
        </Grid>
    )
}


export const ProjectOverview: FC<ProjectProps> = ({
    name,
    date,
    body,
    image,
    url
}) => {

    return (
        <ViewManager
            mobileView={
                <MobileProject
                            name={name}
                            date={date}
                            body={body}
                            url={url}
                            image={image}
                        />
            }
            browserView={
                <BrowserProject
                    name={name}
                    date={date}
                    body={body}
                    url={url}
                    image={image}
                />
            }
        />
    )
}
