import * as React from "react"
import {FC, ReactElement, useEffect, useState,} from "react";

type ViewManagerProps = {
    browserView: ReactElement,
    mobileView: ReactElement
}

export const ViewManager: FC<ViewManagerProps> = ({
    browserView,
    mobileView
}) => {

    if (typeof window === `undefined`) {
        return(<></>);
    }

    const mobileScreenWidth = 900
    const [isMobile, setIsMobile] = useState(window.innerWidth < mobileScreenWidth)

    useEffect(() => {
        function handleWindowResize() {
            setIsMobile(window.innerWidth < mobileScreenWidth)
        }
        window.addEventListener('resize', handleWindowResize);
        return () => {
            window.removeEventListener('resize', handleWindowResize);
        };
    }, []);

    return (
        <>
            {
                isMobile?
                    mobileView :
                    browserView
            }
        </>
    )
}
